<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right mt-4">
            <button class="btn btn-primary" data-toggle="modal"
            @click="openModal(true)" ></button> <!--bt Button trigger modal -->
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                <!-- CSSbs   -->
                <th width="130"></th>
                <th width="100"></th>
                <th width="150"></th>
                <th width="100"></th>
                <th width="120"></th>
                <th width="80"></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item) in products" :key="item.id">
                    <td>{{ item.title }}</td>
                    <td>{{ item.code }}</td>
                    <td>
                        {{ item.due_date }}
                    </td>
                    <td>
                        {{ item.percent}}
                    </td>
                    <td>
                        <!--  is_enabled == 1 -->
                        <span v-if="item.is_enabled" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm"
                            @click="openModal(false, item)"></button>
                        <button class="btn btn-outline-primary btn-sm"
                            @click="deleteModal(item)"></button>
                    </td>
                </tr>
            </tbody>
        </table>
        <pagination :pages="pagination" @turnPage='getProducts'></pagination>

            <!-- bt Modal  -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                <div class="row">
                <div class="col-sm-8">
                    <div class="form-group">
                        <label for="title"></label>
                        <input type="text" class="form-control" id="title"
                            v-model="tempProduct.title"
                            placeholder="">
                    </div>

                    <div class="form-row">
                        <div class="form-group col-md-6">
                            <label for="category"></label>
                            <input type="text" class="form-control" id="category"
                            v-model="tempProduct.due_date"
                            placeholder="">
                        </div>
                        <div class="form-group col-md-6">
                            <label for="price"></label>
                            <input type="unit" class="form-control" id="unit"
                            v-model="tempProduct.code"
                            placeholder="">
                        </div>
                    </div>

                    <div class="form-row">
                        <div class="form-group col-md-6">
                        <label for="origin_price"></label>
                            <input type="number" class="form-control" id="origin_price"
                            v-model="tempProduct.percent"
                            placeholder="">
                        </div>
                    </div>
                    <hr>
                    <div class="form-group">
                    <div class="form-check">
        <!-- is_enabled1    :true-value="1" :false-value="0"-->
                        <input class="form-check-input" type="checkbox"
                            v-model="tempProduct.is_enabled"
                            :true-value="1"
                            :false-value="0"
                            id="is_enabled">
                        <label class="form-check-label" for="is_enabled">
                        
                        </label>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary"
                    data-dismiss="modal"></button>
                <button type="button" class="btn btn-primary"
                    @click="updateProduct"></button>
            </div>
            </div>
        </div>
        </div>

        <!-- bt Modal  -->
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
                <h5 class="modal-title" id="exampleModalLabel">
                <span></span>
                </h5>
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div class="modal-body">
                 <strong class="text-danger">{{ tempProduct.title }}</strong> ()
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-outline-secondary"
                    data-dismiss="modal"></button>
                <button type="button" class="btn btn-danger"
                    @click="confirmDeleteModal"
                ></button>
            </div>
            </div>
        </div>
        </div>

    </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '../../components/Pagination.vue';

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      products: [], // 
      pagination: {}, // 
      tempProduct: {}, // 
      isNew: false, // 
      isLoading: false, // 
      status: { // 
        fileUploading: false,
      },
    };
  },
  methods: {
    // 
    getProducts(page = 1) {
      // ?page=page 
      const api = `${process.env.VUE_APP_APIPATH}/API/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;
      // console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      // main.jsAJAXAJAX
      const vm = this;
      vm.isLoading = true; // 
      vm.$http.get(api).then((response) => {
        vm.isLoading = false; // 
        vm.products = response.data.coupons;
        vm.pagination = response.data.pagination; // pagination
      });
    },
    // 
    openModal(isNew, item) {
      const vm = this;
      if (isNew) {
        vm.tempProduct = {};
        vm.isNew = true;
      } else {
        // this.tempProduct = item;
        // Object.assign({}) item
        vm.tempProduct = { ...item };
        vm.isNew = false;
      }
      $('#productModal').modal('show');
    },
    // API
    updateProduct() {
      let api = `${process.env.VUE_APP_APIPATH}/API/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
      let httpMethpd = 'post';
      // console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      const vm = this;
      // API
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/API/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempProduct.id}`;
        httpMethpd = 'put';
      }
      //  1.API 2.
      // 
      vm.$http[httpMethpd](api, { data: vm.tempProduct }).then((response) => {
        //   
        if (response.data.success) {
          $('#productModal').modal('hide');
          vm.getProducts();
        } else {
          $('#productModal').modal('hide');
          vm.getProducts();
        }
      });
    },
    deleteModal(item) {
      const vm = this;
      vm.tempProduct = { ...item };
      $('#delProductModal').modal('show');
    },
    // 
    confirmDeleteModal() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/API/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempProduct.id}`;
      const httpMethpd = 'delete';
      vm.$http[httpMethpd](api).then((response) => {
        //   
        if (response.data.success) {
          $('#delProductModal').modal('hide');
          vm.getProducts();
        } else {
          $('#delProductModal').modal('hide');
          vm.getProducts();
        }
      });
    },
  },
  // created
  created() {
    this.getProducts();
  },
};
</script>
