import axios from 'axios'; // AJAX
import VueAxios from 'vue-axios'; //  VUe
import Loading from 'vue-loading-overlay'; //  - 
import 'vue-loading-overlay/dist/vue-loading.css'; //  - 
import 'bootstrap';

// 
import {
  ValidationProvider, ValidationObserver, extend, localize,
} from 'vee-validate';
// 
// import { localize } from 'vee-validate'
import TW from 'vee-validate/dist/locale/zh_TW.json';
// email 
import { required, email } from 'vee-validate/dist/rules';

import Vue from 'vue';
import App from './App.vue';
import router from './router';

import currencyFilter from './Filters/currency'; // 
import dateFilter from './Filters/date'; // 

Vue.config.productionTip = false;
Vue.use(VueAxios, axios);
//  API  ()
axios.defaults.withCredentials = true;
Vue.component('Loading', Loading); //  - 

Vue.component('Loading', Loading); //  - 
Vue.filter('currency', currencyFilter); //  :1. 2.import
Vue.filter('date', dateFilter); //  :1. 2.import

new Vue({
  router,
  render: (h) => h(App),
}).$mount('#app');

// next: Function: 
router.beforeEach((to, from, next) => {
  // console.log(to, from, next);
  // requiresAuth 
  // API
  if (to.meta.requiresAuth) {
    // console.log('');
    //  API
    const api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    // axios
    axios.post(api).then((response) => {
    // console.log(response.data);
    //  
      if (response.data.success) {
        next();
        // console.log('');
      } else { // 
        next({
          path: '/login',
        });
        // console.log('');
      }
    });
  } else { // 
    next();
  }
});

// --------------vee-validate------------------------
// https://hao1229.github.io/2019/08/09/EcommercePractice8/

localize('zh_TW', TW);
// Add a rule'secret' value  'example' message 
extend('min', {
  validate(value, args) {
    return value.length >= args.length;
  },
  params: ['length'],
  message: '',
});
// Register it globally
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);

extend('email', email);
extend('required', { //  email 
  ...required,
  message: '',
});
extend('required2', { //  email 
  ...required,
  message: '',
});
// --------------vee-validate------------------------
