<template>
    <table class="table mt-4">
        <thead>
            <tr>
            <!-- CSSbs   -->
            <th width="120"></th>
            <th width="150">Email</th>
            <th width="200"></th>
            <th width="100"></th>
            <th width="100"></th>
            <th width="80"></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in products" :key="item.id">
                    <td>{{ item.create_at | date }}</td>
                    <td>{{ item.user.email }}</td>
                    <td>
                        <ul class="list-unstyled">
                            <li v-for="(product, i) in item.products" :key="i">
                                {{ product.product.title }} {{ product.qty }}
                                {{ product.product.unit }}  {{ product.product.price | currency}}
                            </li>
                        </ul>
                    </td>
                    <td>
                        {{item.total | currency }}
                    </td>
                    <td>
                        <!--   -->
                        <span v-if="item.is_paid" class="text-success"></span>
                        <span v-else></span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm"
                        @click="openData(item)"></button>
                    </td>
                </tr>
        </tbody>

        <!-- bt Modal  -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
            <div class="my-5 row justify-content-center">
                <form  style="background:white; padding:30px"
                    class="col-md-6" @submit.prevent="payOrder">
                    <table class="table">
                    <thead>
                        <th></th>
                        <th></th>
                        <th></th>
                    </thead>
                    <tbody>
                        <tr v-for="item in productData.products" :key="item.id">
                            <td class="align-middle">{{ item.product.title }}</td>
                            <td class="align-middle">{{ item.qty }}</td>
                            <td class="align-middle text-right">
                                {{ item.product.price | currency }}</td>
                        </tr>
                    </tbody>
                    <tfoot>
                        <tr>
                        <td colspan="2" class="text-right">:</td>
                        <td class="text-right">{{ productData.total | currency}}</td>
                        </tr>
                    </tfoot>
                    </table>
                    <table class="table">
                    <tbody>
                        <tr>
                        <th width="100">Email</th>
                        <td>{{  productData.user.email  }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>{{ productData.user.name }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>{{ productData.user.tel }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>{{ productData.user.address }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>
                            <span v-if="productData.is_paid"></span>
                            <span v-else></span>
                        </td>
                        </tr>
                    </tbody>
                    </table>
                </form>
            </div>
        </div>
    </div>

        <pagination :pages="pagination" @turnPage='getProducts'></pagination>

    </table>
</template>

<script>
import $ from 'jquery';
import Pagination from '../../components/Pagination.vue';
// jQueryBootstrap main.jsBootstrap
// JQ 

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      products: [], // 
      pagination: {}, // 
      productData: { // 
        user: {},
      },
      isLoading: false, // 
    };
  },
  methods: {
    getProducts(page = 1) {
      // ?page=page 
      const api = `${process.env.VUE_APP_APIPATH}/API/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`;
      const vm = this;
      vm.isLoading = true; // 
      vm.$http.get(api).then((response) => {
        vm.isLoading = false; // 
        vm.products = response.data.orders;
        vm.pagination = response.data.pagination; // pagination
      });
    },
    openData(item) {
      const vm = this;
      vm.productData = item;
      $('#delProductModal').modal('show');
    },

  },
  created() {
    this.getProducts();
  },
};
</script>
